import React from "react"
import { Link } from "gatsby"

import SEO from "../components/seo"

export default () => (
  <section className="hero is-fullheight">
    <SEO title="Inicio" keywords={[`gatsby`, `application`, `react`]} />
    <div className="hero-body">
      <div className="container has-text-centered">
        <h1 className="title is-size-1">
          Hola, soy Juan Latorre
        </h1>
        <h2 className="subtitle is-size-4">
          Desarrollador web, m√≥vil y de videojuegos.
        </h2>
        <div className="is-divider"></div>
        <Link className="index button is-link is-outlined" to="/blog/">Blog</Link>
        <Link className="index button is-link is-outlined" to="/trabajos/">Trabajos</Link>
        <a href="https://twitter.com/unnamed_query" className="index button is-link is-outlined">Twitter</a>
        <a href="https://github.com/juanlatorre" className="index button is-link is-outlined">Github</a>
        <Link className="index button is-link is-outlined" to="/contacto/">Contacto</Link>
      </div>
    </div>
  </section>
)
